import React, { useContext } from 'react';
import { Redirect, useLocation } from 'react-router-dom';
import { AnimatedList, ChatInput } from '..';
import './styles.scss';
import { MessageContext } from '../../contexts/messages';
const Chat: React.FC = () => {
    const { push, messages, status, name } = useContext(MessageContext);
    const location = useLocation();
    return location && (location.pathname === `/chat` || location.pathname === `/`) ? (
        <div className="chat">
            <div className="intro">
                <p>
                    Hi! From now your name is <span className="name">{name}</span> ðŸ˜Š
                </p>
                <p>(Open another window to see this chat working)</p>
            </div>
            <AnimatedList messages={messages} status={status} />
            <ChatInput push={push} />
        </div>
    ) : (
        <Redirect
            to={{
                pathname: `/info`,
                state: { from: location },
            }}
        />
    );
};

export default Chat;
